import Vue from 'vue'
import Router from 'vue-router'
import lee from './../components/lee.vue'
import lee2 from './../components/lee2.vue'
import lee3 from './../components/lee3.vue'
import lee4 from './../components/lee4.vue'
import lee5 from './../components/lee5.vue'
import lee6 from './../components/lee6.vue'
import lee7 from './../components/lee7.vue'
import gw from './../components/gw.vue'
import lee8 from './../components/lee8.vue'
Vue.use(Router)

const router = new Router({
	routes:[
	{
		path:'/',
		component:lee
	},
	{
		path:'/all',
		component:lee2
	},
	{
		path:'/trans',
		component:lee3
	},
	{
		path:'/zhifu',
		component:lee4
	},
	{
		path:'/guanyu',
		component:lee5
	},
	{
		path:'/xianxia',
		component:lee6
	},
	{
		path:'/gwc',
		component:gw
	},{
		path:'/xw:id',
		component:lee7
	}
	,{
		path:'/sp:name',
		component:lee8
	}
	]
})
export default router


