						txt:['户外运动品牌探路者联合新浪体育在京举办“SNOW ONE超级雪滑雪季暨明星邀请赛”战略合作发布会，正式宣布今冬比赛拉开帷幕。探路者联合创始人、登山探险家王静表示，希望凭借探路者“勇敢的心”品牌理念的驱动，联动知名媒体平台新浪体育、国内顶级商业化雪场、以及具有正能量号召力的明星，唤起大众对滑雪运动和健康生活方式的热爱与关注，让更多人走上冰雪。','未来，探路者将继续为大众提供户外及运动的极致产品和服务，在北京成功申办2022年冬奥会的积极效应下，通过积极的科技研发和多年的产品经验积累，配合外部合作模式的不断升级，引领户外行业竞争开启新的征程。']

