						type: ['全部资讯', '店长博客'],
						txt: ['十二节气里的“霜降”已经来临。这也是秋季的最后一个节气，意味着冬天的到来。虽然天气逐渐转凉，但出游的心还是按捺不住。冬日与白雪来一次约会，这应该是每一个南方人的梦想。而对于北方人来说，可以看去温暖的南方看看海。随着冬季旅游市场逐渐升温，游客可以在国内赏银杏、赏雪、看海。而且现阶段正好处于旅游淡季，出游的成本也不高。不太会滑雪的，可以坐上索道缆车居高俯视整个被白雪覆盖了的度假区，穿上“雪衣”的原始山林就在你的脚下。登上度假区的最高点，遥望远处，心旷神怡，还有冰凌雪花挂在树上，神奇的童话世界尽在眼前。','除了滑雪，长白山还有各种雪地娱乐。这里还有能满足全家人娱雪需求的佛库伦雪圈公园，冰雪世界、狗拉雪橇、驯鹿爬犁……让你尽享童真趣味。','当然，今年雪季与以往雪季会有一些改变，长白山度假区内增加了海东青娱雪乐园、雪地公园、香蕉公园等多项亲子及成人娱乐细分设施，小朋友可以单独在海东青娱雪公园里与雪亲密接触，雪友们可以在雪地公园挑战不同雪道，感受滑雪带来的乐趣，单板滑雪发烧友可以在香蕉公园里挑战“大香蕉”障碍，提高技能。','改变不只于此，度假区内的酒店也有很多新玩点。长白山凯悦酒店还结合长白山原始山林特色推出了“森林王国”主题亲子房，房间内的小松鼠KAKA正等着小朋友的到来。长白山万达假日度假酒店和长白山万达套房假日酒店的BOOMI主题亲子房，除了房间内充满BOOMI元素，还为小朋友们准备了专属的BOOMI书包、行李卡等物件，让BOOMI小熊陪伴小朋友的长白山之行。长白山万达宜必思尚品酒店除了房间内的亲子元素外，还打造了室内游乐园。']

					},
