<template>
	<div>
				<div class="lee6_container" style="margin: 30px;padding:0 40px;">
					<div style="box-shadow: 0 0 1px 1px grey;border-radius:5px ;">
						<h5 style="padding: 50px 25px 30px;font-size:28px;color: #000000;">CONTACT US</h5>
						<p style="font-family:微软雅黑;	color:#000000;font-size:20px;font-weight: bold;padding-left: 25px;margin-bottom: 0;">线下实体</p>
						<p style="padding: 5px 25px 8px;font-family:微软雅黑;color:#000000;font-size:14px;">全国旗舰总店</p>			<div class="imgs">
							<div id="allmap" style="height: 50%;min-height: 500px;">

					</div>
							<div style="padding: 20px 25px;">
								<p style="font-size:13px;color:#696969;font-family:微软雅黑;">
									地址&nbsp;:&nbsp; 江苏省苏州市工业园区金鸡湖大道99号	
								</p><p style="font-size:13px;color:#696969;font-family:微软雅黑;">
									电话&nbsp;:&nbsp; 400 6666 6666	
								</p>
							</div>
						</div>
					</div>
				</div>
			</div>
</template>

<script>
	import BMap from 'BMap'
	export default {
		mounted() {
			var map = new BMap.Map("allmap"); // 创建Map实例
			map.centerAndZoom(new BMap.Point(116.404, 39.915), 11); // 初始化地图,设置中心点坐标和地图级别
			//添加地图类型控件
			map.addControl(new BMap.MapTypeControl({
				mapTypes: [
					BMAP_NORMAL_MAP,
					BMAP_HYBRID_MAP
				]
			}));
			map.centerAndZoom("苏州",15);  
			map.setCurrentCity("苏州");          // 设置地图显示的城市
			map.enableScrollWheelZoom(true); //开启鼠标滚轮缩放
		}
	}
</script>

<style>
</style>


