<template>
  <div id="app">
    <div class="container-fluid" id="box">
			<alee></alee>
			<router-view></router-view>
			<alee2></alee2>
		</div>
  </div>
</template>

<script>
	import alee from './components/alee.vue'
	import alee2 from './components/alee2.vue'
export default {
  name: 'app',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  },
  components:{
  	alee,
  	alee2
  }
}
</script>

<style>
	*{margin: 0;padding: 0;list-style: none;text-decoration: none;}
.container-fluid{
	    padding-right: 0;
    padding-left: 0;
}
.maxwi{
	max-width: 1190px!important;
	margin: 0 auto;
}
.hover{
	padding: 2px 0;
	background: #e0e0e0 none repeat center center;
}
.hover p{
	    color: #494949;
    font-size: 12px;
    line-height: 40px;
    margin: 0;
}
.navs:hover{
	background:#1585d4;
}
.navsactive {
	background:#1585d4;
}
.navs{
	margin: 0;
	height: 36px;
	margin:5px 5px 0 0;
	line-height: 36px;
	text-align: center;
	color: white;
}
.navs a{
	color: white;
	text-decoration: none;
}
@font-face {
  font-family: 'iconfont';  /* project id 789026 */
  src: url('//at.alicdn.com/t/font_789026_vij1c2knfxl.eot');
  src: url('//at.alicdn.com/t/font_789026_vij1c2knfxl.eot?#iefix') format('embedded-opentype'),
  url('//at.alicdn.com/t/font_789026_vij1c2knfxl.woff') format('woff'),
  url('//at.alicdn.com/t/font_789026_vij1c2knfxl.ttf') format('truetype'),
  url('//at.alicdn.com/t/font_789026_vij1c2knfxl.svg#iconfont') format('svg');
}
.iconfont{
    font-family:"iconfont" !important;
    font-size:16px;font-style:normal;
    -webkit-font-smoothing: antialiased;
    -webkit-text-stroke-width: 0.2px;
    -moz-osx-font-smoothing: grayscale;}
</style>



