import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)
const state={
	lee:'hello vuex',
	arr:[],
	zj:0
}
const getters={
	lee(data){
		return data.lee
	},
	arr(data){
		return data.arr
	},
	zj(data){
		var str =0;
		for (var i=0;i<data.arr.length;i++) {
			str += data.arr[i].n * data.arr[i].jg
		}
		return str
	}
}
const mutations={
	mutations_arr(data,a){
		var gwcarrkg=true;;
		if (data.arr.length==0) {
			gwcarrkg=true;
		} else{
			for (var i=0;i<data.arr.length;i++) {
				if (data.arr[i].id == a.id) {
					data.arr[i].n++
					gwcarrkg=false;
				} 
			}
		}
		if(gwcarrkg){
			data.arr.push(a)
		} 
	},
	gwcxjie(data,a){
		for (var i=0;i<data.arr.length;i++) {
			if (data.arr[i].id == a) {
				data.arr[i].n++;
			}
		}
	},
	gwcxjia(data,a){
		for (var i=0;i<data.arr.length;i++) {
			if (data.arr[i].id == a) {
				data.arr[i].n--;
				if(data.arr[i].n<1){
					data.arr[i].n=1
				}
			}
		}
	},
	removearr(data,a){
		for (var i=0;i<data.arr.length;i++) {
			if (data.arr[i].id == a) {
				data.arr.splice(i,1)
			}
		}
	}
}
const actions={
	actions_arr({commit},data){
		commit('mutations_arr',data)
	},
	gwcx({commit},data){
		commit('gwcxjia',data)
	},
	gwcs({commit},data){
		commit('gwcxjie',data)
	},
	remove({commit},data){
		commit('removearr',data)
	}
}
export default new Vuex.Store({
	state,
	getters,
	mutations,
	actions
})



