<template>
	<div class="row" style="padding: 0;margin: 0;">
		<div v-if="arr[0]" style="padding: 40px 80px 70px;">
			<table border="" cellspacing="" cellpadding="" style="width: 100%;text-align: center;">
				<tr>
					<td>id</td>
					<td>商品</td>
					<td>价格</td>
					<td>数量</td>
					<td>小计</td>
					<td>操作</td>
				</tr>
				<tr v-for="i in arr">
					<td>{{i.id}}</td>
					<td>{{i.txt}}</td>
					<td>{{i.jg}}</td>
					<td>
						 <button @click="gwcs(i.id)" >+</button>
        <span>{{i.n}}</span>
        <button @click="gwcx(i.id)">-</button>
					</td>
					<td><span>¥{{i.n*i.jg}}</span></td>	
					<td>
						<button @click="remove(i.id)" >删除</button>
					</td>
				</tr>

		  	</table>
		  	<p style="padding: 10px 0;text-align: right;">总计：¥{{zj}}</p>
		</div>
		<div v-else>
			购物车没有数据
		</div>
	</div>
</template>

<script>
	import {mapGetters,mapActions} from 'vuex'
	export default{
		props:['gwdata'],
		data(){
			return{
				data:this.gwdata
			}
		},
		computed:mapGetters(['arr','zj']),
		methods:{
			...mapActions(['gwcx','remove','gwcs'])
		}
	}
</script>

<style>
</style>


